.services {
  @include content-padding-inline;

  margin-bottom: 120px;

  @include on-tablet {
    margin-bottom: 148px;
  }

  @include on-small-desktop {
    margin-bottom: 151px;
    display: flex;
    height: 687px;
  }

  &__content {
    @include page-grid;
  }

  &__info {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-small-desktop {
      grid-column: span 4;
      padding-top: 103px;
    }

    &-heading {
      margin: 0;

      font-family: Poppins, sans-serif;
      font-weight: 700;
      font-size: 13px;
      line-height: 19.5px;
      letter-spacing: 3px;
      text-align: left;
      text-transform: uppercase;
      color: $accent-grey-color;
    }

    &-title {
      margin: 0;
      padding-top: 16px;
      padding-bottom: 24px;

      font-family: Poppins, sans-serif;
      font-weight: 600;
      font-size: 32px;
      line-height: 41.6px;
      color: $color-for-light-bg;
      text-align: left;
    }

    &-description {
      margin: 0;
      padding-bottom: 48px;

      font-family: 'Open Sans', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: $color-for-light-bg-description;

      @include on-tablet {
        padding-bottom: 56px;
      }
    }
  }

  &__cards {
    grid-column: 1 / -1;

    @include on-tablet {
      @include page-grid;
    }

    @include on-small-desktop {
      position: relative;
      top: -45%;
    }
  }

  &__card {
    background-color: $color-for-dark-bg;
    border-radius: $border-radius-for-img;

    padding: 32px 48px 32px 32px;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

    @include on-tablet {
      padding: 32px;

      &-first {
        grid-column: 1 / 4;

        @include on-small-desktop {
          grid-column: 7 / 10;
        }
      }

      &-second {
        grid-column: 4 / 7;
        transform: translateY(-130px);

        @include on-small-desktop {
          grid-column: 10 / 13;
          transform: translateY(-140px);
        }
      }

      &-third {
        grid-column: 1 / 4;

        @include on-small-desktop {
          grid-column: 7 / 10;
        }
      }

      &-fourth {
        grid-column: 4 / 7;
        transform: translateY(-130px);

        @include on-small-desktop {
          grid-column: 10 / 13;
          transform: translateY(-140px);
        }
      }
    }

    &-number {
      margin: 0;

      font-family: Poppins, sans-serif;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
      color: $accent-color;
      display: flex;
      width: 46px;
      height: 24px;
      border-radius: 23px;
      background-color: rgba($accent-color, 0.1);
      justify-content: center;
      align-items: center;
    }

    &-info {
      margin: 0;
      padding-top: 24px;
      padding-bottom: 32px;

      font-family: 'Open Sans', sans-serif;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
      text-align: left;
      color: $color-for-light-bg-description;
    }

    &-button {
      display: flex;
      align-items: center;
      gap: 16px;

      &-design {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: $accent-color;
        box-sizing: border-box;
        background-color: $accent-color;

        margin: 0;
        padding: 0;

        transition: transform 0.3s;
        cursor: pointer;

        &:hover {
          transform: scale(1.1);
        }
      }

      &-additional {
        font-family: Poppins, sans-serif;
        font-size: 15px;
        font-weight: 600;
        line-height: 22.5px;
        color: $additional-color;
      }
    }
  }
}
