.header {
  background-color: $header-bg-color;
  color: $color-for-dark-bg;
  border-bottom-right-radius: $border-radius-for-img;
  border-bottom-left-radius: $border-radius-for-img;

  height: 800px;

  &__container {
    @include page-grid;
    @include content-padding-inline;

    @include on-small-desktop {
      padding-top: 168px;
      padding-right: 0;
      display: flex;
      justify-content: space-between;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding-top: 20px;
    padding-bottom: 48px;

    @include on-tablet {
      padding-bottom: 56px;
    }

    @include on-small-desktop {
      padding-top: 48px;
      padding-bottom: 44px;
    }
  }

  &__title {
    @include page-grid;

    grid-column: 1 / -1;

    @include on-small-desktop {
      grid-column: 1 / 6;
      padding: 0;
      width: 490px;
    }

    @include on-desktop {
      grid-column: 1 / 3;
    }

    &-text {
      margin: 0;

      grid-column: 1 / -1;
      font-family: Poppins, sans-serif;
      font-size: 52px;
      font-weight: 600;
      line-height: 62.4px;
      letter-spacing: -2px;
      text-align: left;

      @include on-tablet {
        font-size: 64px;
        line-height: 68px;
      }

      @include on-small-desktop {
        grid-column: 1 / 3;
      }
    }

    &-description {
      grid-column: 1 / -1;
      font-family: 'Open Sans', sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 27px;
      text-align: left;
      margin-top: 32px;
      margin-bottom: 72px;

      @include on-tablet {
        grid-column: span 4;
        margin-top: 24px;
        margin-bottom: 56px;
      }

      @include on-small-desktop {
        grid-column: 1 / 10;
        margin-top: 32px;
        margin-bottom: 83px;
      }
    }

    &-button {
      text-decoration: none;
      grid-column: 1 / -1;

      @include on-tablet {
        grid-column: 1 / 4;
      }

      @include on-small-desktop {
        grid-column: 1 / 8;
      }

      &-design {
        font-family: Poppins, sans-serif;
        font-size: 15px;
        font-weight: 600;
        line-height: 22.5px;
        text-align: center;
        color: $color-for-dark-bg;

        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 50px;
        border-radius: 8px;
        background-color: $accent-color;
        cursor: pointer;
        margin: 0;

        @include hover(transform, scale(1.1));
      }
    }
  }

  &__slider {
    display: flex;
    position: absolute;
    inset: 503px 0 0;
    height: 390px;
    transition: transform 0.3s ease-in-out;

    @include on-small-desktop {
      width: 50%;
      height: 680px;
      top: 120px;
      right: 0;
      margin-left: auto;
    }

    @include on-desktop {
      width: 41.6%;
    }

    &-img {
      display: flex;
      width: 100%;
      overflow: hidden;
      object-fit: fill;
      flex-shrink: 0;

      &-design {
        border-radius: $border-radius-for-img;
        width: 100%;
        height: auto;
        object-fit: cover;

        @include on-small-desktop {
          border-bottom-left-radius: 0;
          border-top-right-radius: 0;
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      position: absolute;
      bottom: 24px;
      padding-left: 20px;

      @include on-tablet {
        bottom: 32px;
        padding-left: 41px;
      }

      @include on-small-desktop {
        flex-direction: row;
        align-items: center;
        gap: 24px;
        padding-left: 78px;
        bottom: 48px;
      }
    }

    &-buttons {
      display: flex;
      flex-direction: row;
      gap: 16px;

      &-design {
        display: flex;
        background-color: $color-for-dark-bg;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        align-items: center;
        justify-content: center;

        @include hover(transform, scale(1.1));
      }
    }

    &-text {
      color: $color-for-dark-bg;

      &-title {
        font-family: Poppins, sans-serif;
        font-size: 13px;
        font-weight: 700;
        line-height: 19.5px;
        letter-spacing: 3px;
        text-transform: uppercase;
        margin-top: 16px;
        margin-bottom: 8px;

        @include on-small-desktop {
          margin-top: 0;
        }
      }

      &-description {
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;
        font-weight: 400;
        line-height: 19.6px;
        text-align: left;
        margin: 0;
      }
    }
  }
}
